/*
 * Archive Page Styles
 */

$width-primary-content: calc((824 / 1180) * 100%);
$width-secondary-content: calc((324 / 1180) * 100%);

.site-header {
	@include breakpoints(large) {
		margin-bottom: spacer(5);
	}
}

.site-content {
	@include breakpoints(medium) {
		margin-bottom: spacer(4);
	}
}

.site-content__primary {
	@include breakpoints(medium) {
		margin-bottom: 0;
	}
}

.archive-content-wrapper {
	@include vertical-rhythym(rem(84));
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		padding: 0;
	}

	@include breakpoints(large) {
		margin-bottom: rem(70);
		flex-direction: row;
	}
}

.archive-content__primary {
	@include vertical-rhythym(spacer(5));

	@include breakpoints(medium) {
		@include vertical-rhythym(rem(82));
		margin-bottom: rem(56);
	}

	@include breakpoints(large) {
		@include vertical-rhythym(rem(68));
		margin-bottom: 0;
	}

	.archive-sidebar--right & {
		@include breakpoints(large) {
			width: $width-primary-content;
			margin-right: spacer(2);
			padding-right: spacer(2);
		}
	}

	.archive-sidebar--none & {
		width: 100%;
	}
}

.archive-content--term {
	@include vertical-rhythym(spacer(5), spacer(5), $border-light);

	@include breakpoints(medium) {
		@include vertical-rhythym(rem(82), rem(82), $border-light);
	}

	@include breakpoints(large) {
		@include vertical-rhythym(rem(68), rem(68), $border-light);
	}
}

.archive__page-header {
	@include section-header();

	@include breakpoints(medium) {
		padding-bottom: rem(40);
		margin-bottom: spacer(3);
	}

	@include breakpoints(large) {
		padding-bottom: spacer(2);
		margin-bottom: rem(36);
	}
}

.archive__page-title {
	@include page-title();
}

.archive-section--term {
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		flex-direction: row;
		flex-wrap: wrap;
	}

	.content-column__column--large {
		flex: 1 0 auto;
	}
}

.archive-section__header {
	margin-bottom: spacer(1);

	@include breakpoints(large) {
		margin-bottom: rem(10);
	}

	@include breakpoints(medium) {

		.archive-section--term & {
			width: rem(180);
			margin-right: rem(60);
		}
	}
}

.archive-section__title {
	@include section-title();
}
