/*
 * Archive Post List section styles
 */

// Common Post List Styles
.post-list--tout {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	position: relative;

	// Load More Post Tout Styles
	&.wrap-ajax-loaded {
		margin-top: rem(20);
	}
}

.post-list--posts {
	width: 100%;
}

.post-list__item--tout {
	border-bottom: none;
	flex: 0 1 100%;
	height: rem(285);
	margin-bottom: rem(20);
	overflow: hidden;
	position: relative;

	&:nth-child(2n) {
		margin-right: 0;
	}

	@include breakpoints(medium) {
		flex-basis: calc((100% / 2) - (#{rem(20)} * (1 / 2)));

		&:nth-last-child(-n+2) {
			margin-bottom: 0;
		}
	}

	@include breakpoints(large) {
		height: rem(260);
	}
}

.post-list__item-content-wrapper,
.post-list__item-link-wrapper {
	@include absolute-pinned();
}

.post-list__item-content-wrapper {
	background: fade-out($white, 0.25);
	border: $border-light;
	padding: rem(20);

	&:hover {
		border-color: $poppy;

		/* stylelint-disable selector-max-specificity */
		.post-list__item-title-link {
			color: $poppy;
		}
		/* stylelint-enable */
	}
}

.post-list__item-link-wrapper {
	@include hide-text();
	z-index: z-index(neutral);
}

.post-list__item-title,
.byline--post-list {
	position: relative;
	z-index: z-index(first);
}

.post-list__item-title {
	@include post-header();
	font-weight: 500;
}

.post-list__item-title-link {
	display: block;

    &[href*="future.com"],
    &[href*="future.a16z.com"] {
        display: inline-block;
        position: relative;
        padding-right: rem(30);

        &::after {
            content: "";
            position: absolute;
            right: 0;
            top: 0;
            width: rem(30);
            height: rem(28);
            background: url('../../images/logo-icon-future.svg') no-repeat 50% 50% / rem(20) rem(11);
        }
    }
}

.byline--post-list {
	display: inline;
}

.byline__prefix,
.byline__authors {
	line-height: rem(20);
}

.byline__prefix {
	margin-right: spacer(0.125);
}

.byline__authors {

	a {
		display: inline-block;
	}
}
