/*
 * Search results archive styles
 */

.page-header--search {
	flex-direction: column;

	.page-header__wrapper {
		display: flex;
		flex-direction: column;

		@include breakpoints(medium) {
			flex-direction: row;
		}
	}

	.page-header__title,
	.search-form--results-header {
		width: 100%;
		flex: 1 1 auto;
	}

	.page-header__title {
		@include breakpoints(medium) {
			width: calc( 100% - #{rem(360)} );
		}
	}

	.search-form--results-header {
		margin-bottom: spacer(2);

		@include breakpoints(medium) {
			width: rem(360);
		}
	}

	.results-filter {
		width: 100%;
	}
}
