.team-member__content-wrapper > a {
	text-decoration: none;
}

.team-member__bio {
	margin-bottom: spacer(3);
}

.team-member__photo-wrapper {
	margin-bottom: spacer(2);
	width: 100%;

	@include breakpoints(small) {
		float: left;
		margin-right: spacer(2);
		max-width: rem(160);
		width: auto;
	}
}

.team-member__photo {
	margin-bottom: spacer(0.5);
	width: 100%;
	height: 100%;
}

.team-member__social-links {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.team-member__link {
		margin-right: 10px;
	}
}

.team-member__link {
	@include action-link;
	display: block;
	margin-bottom: spacer(0.5);
}

.team-member__link--download {
	border-bottom: $border-light;
	padding-bottom: spacer(0.5);
}

.author__posts {
	clear: both;
}

.author__ad-slot {
	margin-bottom: spacer(4);
}

.section-title--author {
	@include section-subtitle();
	margin-bottom: spacer(1);
}

.team-member__tw {
  display: flex;
  align-items: center;
  background: #55acee;
  padding: 6px 20px;
  font-size: 22px;
  border-radius: 0;
  justify-content: center;
  margin: 10px 0;
  text-decoration: none;
  color: $white;
  transition: 0.2s;

  img {
    width: 22px;
    margin-right: 10px;
  }

  @include breakpoints(medium) {
    display: none;
  }
}
