.page-template-page_template-content {

	.site-content {
		padding-top: spacer(2);

		@include breakpoints(medium) {
			padding-top: spacer(1.25);
		}

		@include breakpoints(large) {
			padding-top: 0;
		}
	}

	.page-content--primary {
		@include vertical-rhythym(spacer(1.75), spacer(3.5), $border-light);

		@include breakpoints(medium) {
			@include vertical-rhythym(spacer(2), spacer(4), $border-light);
		}
	}
}

.page-header__title,
.categories-list--header-nav {
	width: 100%;
}

.page-header__title {
	font-size: rem(45);

	@include breakpoints(medium) {
		font-size: rem(60);
	}
}

.categories-list--header-nav,
.categories-list--header-sort {
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		flex-direction: row;
	}

	.categories-list__title {
		color: $color-secondary;
		text-transform: uppercase;
		margin-bottom: spacer(0.75);
		font-weight: 500;
		font-size: rem(18);
		margin-bottom: 0;

		@include breakpoints(medium) {
			margin-right: spacer(1);
			font-size: rem(16);
		}
	}

	.categories-list__list {
		display: flex;
		flex-direction: column;

		@include breakpoints(medium) {
			flex-direction: row;
		}
	}

	.categories-list__item {
		margin-right: spacer(1);
		overflow: hidden;
	}

	.categories-list__item-link {
		@include link-hover($color-secondary);
		display: block;
		text-transform: capitalize;
		white-space: nowrap;

		@include breakpoints(medium) {
			font-weight: 500;
		}
	}

	.categories-list__item-link--active {
		color: $poppy;
	}
}

.categories-list--topics {
	@include breakpoints(medium) {
		column-gap: rem(20);
		column-count: 2;
		flex: 1 1 auto;
		width: 100%;
	}

	@include breakpoints(large) {
		column-count: 3;
	}

	.categories-list__item {
		margin-bottom: spacer(0.75);

		@include breakpoints(medium) {
			margin-bottom: rem(14);
		}
	}

	.categories-list__item-link {
		@include link-hover();
	}
}

.categories-list--tech,
.categories-list--advice {
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		flex-direction: row;
	}

	.categories-list__title {
		@include section-title();
		width: 100%;
		margin-bottom: spacer(2.75);

		@include breakpoints(medium) {
			flex: 0 0 auto;
			margin: 0 spacer(7) 0 0;
			width: spacer(11);
		}

		@include breakpoints(large) {
			margin: 0 spacer(9) 0 0;
		}
	}

	.categories-list__list {
		flex-flow: column;

		@include breakpoints(medium) {
			column-gap: rem(20);
			column-count: 2;
			flex: 1 1 auto;
			width: 100%;
		}

		@include breakpoints(large) {
			column-count: 3;
		}
	}

	.categories-list__item {
		break-inside: avoid;
		display: inline-block;
		margin-bottom: spacer(0.75);
		width: 100%;

		@include breakpoints(medium) {
			margin-bottom: rem(14);
		}
	}

	.categories-list__item-link {
		@include link-hover();
	}
}
