/*
 * Archive sidebar section styles
 */

.archive-content__secondary {
	@include breakpoints(large) {
		padding-top: rem(20);
		width: $width-secondary-content;
	}
}

.widget-area {
	@include vertical-rhythym(spacer(2));

	@include breakpoints(medium) {
		@include vertical-rhythym(0);
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	@include breakpoints(medium) {
		flex-direction: column;
	}

	section {

		&:first-child {
			padding-top: 0;
		}
	}
}

.widget--content {

	.widget__title {
		@include breakpoints(medium) {
			margin-bottom: rem(6);
		}
	}
}

.widget--rss-link {
	margin-bottom: rem(8);

	@include breakpoints(medium) {
		padding-top: rem(52);
	}

	@include breakpoints(large) {
		margin-bottom: spacer(2);
	}
}

.rss-link__link {
	color: $steel;
	display: flex;
	font-weight: 500;
	margin-bottom: rem(4);
	text-decoration: none;

	&:hover {
		color: $poppy;

		/* stylelint-disable */
		/* Allow selecting clildren of hovered element */
		.icon--rss {
			background-color: $poppy;
		}
		/* stylelint-enable */
	}

	&:last-child {
		margin-bottom: 0;
	}
}

.icon {
	margin-right: spacer(0.5);

	@include text-replace(rem(24), rem(24));

	&:hover {
		color: $poppy;
	}

	&.icon--rss {
		@include svg-mask($steel, '../../images/icon-rss.svg');
	}

	&.icon--soundcloud {
		@include svg-mask($steel, '../../images/icon-soundcloud.svg');
	}

	&.icon--simplecast {
		@include svg-mask($steel, '../../images/icon-simplecast.svg');
	}
}

.widget--posts {
	display: none;

	@include breakpoints(large) {
		display: block;
		padding-top: rem(40);

		.widget__title {
			margin-bottom: spacer(1);
		}
	}

	.post-list__item {
		border-bottom: none;
		margin-bottom: spacer(1);
	}

	.post-list__item-title {
		line-height: 1.25;
		font-size: rem(16);
		margin-bottom: 0;
	}

	.byline--post-list,
	.post-terms--post-list {
		display: none;
	}

	.post-list__item-read-more {
		display: block;
		font-weight: 500;
	}
}

// Archive Widget Area Overrides
.sidebar--archive {

	.widget__title {
		font-size: rem(24);
		text-transform: none;
		margin-bottom: rem(8);

		@include breakpoints(large) {
			font-size: rem(16);
			text-transform: uppercase;
			margin-bottom: rem(6);
		}
	}
}
