.page-header {
	@include section-header();

	@include breakpoints(medium) {
		padding-bottom: rem(40);
		margin-bottom: spacer(3);
	}

	@include breakpoints(large) {
		padding-bottom: spacer(2);
	}

	&.page-header--content,
	&.page-header--team {
		display: flex;
		flex-direction: column;
		padding-bottom: spacer(4);

		@include breakpoints(medium) {
			padding-bottom: spacer(1.5);
			margin-bottom: spacer(2);
		}
	}

	&.page-header--team {
		padding-bottom: spacer(1.5);
	}

	&.page-header--jobs {
		margin-bottom: 0;
	}

	a {
		color: inherit;
		text-decoration: none;
	}

	.sub-pages {
		margin-left: rem(10);
		margin-top: rem(10);
	}

	.sub-page {
		display: inline-block;
		margin-left: rem(10);
	}
}

.page-header__title {
	@include page-title();
	display: block;

	.page-header--has-subnav & {
		margin-bottom: spacer(1.5);
	}
}

.page-header__title--small {
	border-bottom: rem(1) solid $border-light;
	font-size: rem(31.5);
	letter-spacing: rem(-0.2);
	line-height: 1.46;
	margin-bottom: spacer(2);
	padding-bottom: rem(16);
}

.page-header__subheader {
	font-size: rem(20);
	line-height: rem(24);
	margin-bottom: spacer(1);
}

.page-header__subnav {
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		flex-direction: row;
	}
}

.page-header__subnav-list {
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		flex-direction: row;
	}
}

.page-header__subnav-item {
	margin-right: spacer(1);

	a {
		@include action-link;
	}
}

body.page-template-page_template-contact {

	p {
		margin-bottom: 1rem;
	}
}
